import { getRequestList } from "@/api/modules/web/request";
import FilterHeader from "@/components/FilterHeader";
import { Card, Table, message } from "antd";
import { ColumnsType } from "antd/es/table";
import { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";

interface DataType {
    key: React.Key;
    api: string;
    requestMethod: string;
    requestUrl: string;
    // requestTime: string;
    // requestHeaders: string;
    // requestBody: string;
    // responseStatus: string;
    // responseTime: string;
    // responseHeaders: string;
    // responseBody: string;
    duration: number;
}

const RequestList = () => {

    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [requestTotal, setRequestTotal] = useState(0);
    const [requestList, setRequestList] = useState<DataType[]>([]);
    const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);
    const [searchParams] = useSearchParams();
    const [messageApi, contextHolder] = message.useMessage();

    const columns: ColumnsType<DataType> = [
        {
            title: 'api',
            dataIndex: 'api',
            width: 100,
        },
        {
            title: '请求方法',
            dataIndex: 'requestMethod',
            width: 200,
        },
        {
            title: '请求URL',
            dataIndex: 'requestUrl',
            width: 200,
        },
        {
            title: '请求时间',
            dataIndex: 'duration',
            width: 200,
        },
    ];

    const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
        setSelectedRowKeys(newSelectedRowKeys);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const initRequestList = async () => {
        const appId = searchParams.get('appId');
        if (!appId) {
            messageApi.open({
                type: 'error',
                content: 'appId不能为空!',
            });
            return;
        }
        const result = await getRequestList({ pageNum, pageSize, appId });
        if (result.data) {
            setRequestTotal(result.data.total);
            const data: DataType[] = [];
            result.data.data.forEach(item => {
                data.push({
                    key: item.requestId,
                    api: item.api,
                    requestMethod: item.requestMethod,
                    requestUrl: item.requestUrl,
                    duration: item.duration
                })
            });
            setRequestList(data);
        }
    };

    useEffect(() => {
        initRequestList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        initRequestList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize])

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: requestTotal
    }

    return (<div>
        <FilterHeader></FilterHeader>
        <Card style={{ marginTop: '16px' }} title="请求列表">{contextHolder}<Table style={{ marginTop: '12px' }} rowSelection={rowSelection} pagination={pagination} columns={columns} dataSource={requestList} /></Card>
    </div>);
}

export default RequestList;